import { clsx, type ClassValue } from "clsx";
import { twMerge } from "tailwind-merge";

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

/**
 * formatDate - Format a Date object to a localized date string
 *
 * Converts a Date object to a human-readable date string using the British English
 * locale format (DD MMM YYYY). This provides consistent date formatting across
 * the application for better user experience.
 *
 * @param timestamp - The Date object to format
 * @returns A formatted date string in "DD MMM YYYY" format (e.g., "15 Sep 2025")
 *
 * @example
 * ```typescript
 * const date = new Date('2025-09-15T10:30:00Z');
 * const formatted = formatDate(date);
 * console.log(formatted); // Output: "15 Sep 2025"
 * ```
 *
 * @example
 * ```typescript
 * // Usage in React component
 * const user = { createdAt: new Date() };
 * return (
 *   <div>
 *     <p>Created: {formatDate(user.createdAt)}</p>
 *   </div>
 * );
 * ```
 *
 * @example
 * ```typescript
 * // Usage with current date
 * const today = formatDate(new Date());
 * console.log(`Today is ${today}`); // Output: "Today is 16 Sep 2025"
 * ```
 */
export const formatDate = (timestamp: Date | undefined) => {
  if (!timestamp) return undefined;
  return new Date(timestamp).toLocaleDateString("en-GB", {
    day: "2-digit",
    month: "short",
    year: "numeric",
  });
};

export const calculateDurationSinceJoining = (timestamp: Date): string => {
  const joiningDate = new Date(timestamp);
  const currentDate = new Date();

  let years = currentDate.getFullYear() - joiningDate.getFullYear();
  let months = currentDate.getMonth() - joiningDate.getMonth();
  let days = currentDate.getDate() - joiningDate.getDate();

  // Adjust if days are negative
  if (days < 0) {
    months -= 1;
    const prevMonth = new Date(
      currentDate.getFullYear(),
      currentDate.getMonth(),
      0
    );
    days += prevMonth.getDate();
  }

  // Adjust if months are negative
  if (months < 0) {
    years -= 1;
    months += 12;
  }

  // Build string with plural handling
  const parts: string[] = [];
  if (years > 0) parts.push(`${years} year${years > 1 ? "s" : ""}`);
  if (months > 0) parts.push(`${months} month${months > 1 ? "s" : ""}`);
  if (days > 0) parts.push(`${days} day${days > 1 ? "s" : ""}`);

  return parts.length > 0 ? parts.join(" ") : "0 days";
};

export const formatSalary = (salary: number) => {
  return new Intl.NumberFormat("en-BD", {
    style: "currency",
    currency: "BDT",
    minimumFractionDigits: 0,
  }).format(salary);
};
